<template>
    <div id="article">
        <navigation class="nav"/>
        <div class="content-area area">
            <div class="item" v-for="(item,index) in articles" :key="index" @click="toBlogDetail(item.id)">
                <div class="item-head">{{item.title}}</div>
                <div class="item-content">
                    <span>标签: {{item.label}}</span>
                    <span>类型: {{item.type}}</span>
                    <span>创建时间: {{item.createTime}}</span>
                    <span>更新时间: {{item.updateTime}}</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import navigation from "@/components/navigation_bar";
    import req from "@/utils/req";
    import api from "@/config/api";

    export default {
        data() {
            return {
                articles: null
            }
        },
        mounted() {
            req(api.getArticleList, { label: this.$route.query.label, state: 1 }).then(res => {
                this.articles = res.data;
            });
        },
        methods: {
            toBlogDetail(id) {
                this.$router.push({name: 'blogDetail', query:{ id: id }});
            }
        },
        components: {
            navigation
        },
        beforeRouteEnter(to, from, next) {
            window.document.body.classList.add("blog-background");
            next();
        },
        beforeRouteLeave(to, from, next) {
            window.document.body.classList.remove("blog-background");
            next();
        }
    }
</script>

<style lang="scss">
    .blog-background {
        @include background("../../assets/img/b3.jpg", $width, $repeat);
    }
    #article {
        @include align(24, $column);
    }
    #article {
        .content-area {
            width: $areaWidth;
        }
        .item {
            padding: 1.5rem;
            margin-top: 2rem;
            cursor: pointer;
            border: 2px solid $borderColor;
            background-color: $headBackColor;
        }
        .item:first-child {
            margin-top: 0;
        }
        .item-head {
            @include font(1rem, #333, bold);
        }
        .item-content {
            margin-top: 1rem;
            @include font(0.6rem, #333);
        }
        .item-content > span {
            margin-right: 1rem;
        }
    }
</style>
